.select-component-wrapper {
    .select-component {
        max-width: 240px;
        padding: 0 8px;
        height: 36px;
        display: flex;
        align-items: center;
        border-radius: 8px;
        border: 1px solid #d6d6d6;
        background: #fff;
        transition: border-color 0.2s;

        &:hover {
            border-color: rgba(1, 144, 219, 0.48);
        }

        &:focus {
            outline: none;
            box-shadow: none;
        }

        &__label {
            margin-bottom: 4px;
            color: #5c5c5c;
            font-size: 12px;
            line-height: 16px;
        }

        .ss-values {
            color: #1f1f1f;
            font-size: 14px;
            line-height: 20px;
        }
    }

    .ss-open-below {
        border-color: rgba(1, 144, 219, 0.48);

        border-bottom-right-radius: 8px !important;
        border-bottom-left-radius: 8px !important;
    }
}

.ss-content {
    margin-top: 4px !important;
    padding: 8px;
    border-radius: 8px;
    border-bottom-right-radius: 8px !important;
    border-bottom-left-radius: 8px !important;
    background: #fff;
    box-shadow: 0px 16px 24px 0px rgba(0, 0, 0, 0.06),
        0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);

    .ss-option {
        border-radius: 2px;
        padding: 14px 8px !important;
        color: #1f1f1f !important;
        font-size: 14px;
        line-height: 20px;

        &:hover {
            background-color: rgba(1, 144, 219, 0.08) !important;
        }

        &.ss-selected {
            background-color: rgba(1, 144, 219, 0.08) !important;
        }
    }

    .ss-list {
        &::-webkit-scrollbar {
            width: 4px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            border: 2px solid #d6d6d6;
        }

        &::-webkit-scrollbar-track {
            background-color: #fff;
            border-radius: 10px;
        }
    }
}
